<template>
  <div class="option">
    <div v-if="error" class="option__status">{{msg}}</div>
    <div class="option-select" :class="className">
      <div class="option-select__title"
           @click="toggleMenu">
        <span class="option-select__title-text">{{title}}</span>
      </div>
      <div v-if="listOpen"
           class="option-select__list"
           @click="onListClick"
      >
        <div class="option-select__item"
             :key="option.value"
             :data-value="option.value"
             :class="option.active ? 'option-select__item_active': '' "
             v-for="option in listData">{{option.title}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "Select",
        props: {
            placeholder: {
                type: String,
                default: "Выбрать знаечние"
            },
            multi: {
                type: Boolean,
                default: false,
            },
            listData: {
                type: Array,
                default: () => {
                    return []
                }
            },
            error: {
                type: Boolean,
                default: false,
            },
            msg: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                valueChanged: false,
                selectValueText: "",
                selectValue: null,
                listOpen: false,
                valueList: [],
            };
        },
        computed: {
            title() {
                return !this.valueChanged ? this.placeholder : this.selectValueText;
            },
            className() {
                return {
                    "option-select_open": this.listOpen,
                    "option-select_error": this.error,
                }
            }
        },
        methods: {
            toggleMenu() {
                this.listOpen = !this.listOpen;

                if(this.multi) {
                    this.$emit("onSelect", this.valueList);
                }
            },
            onListClick(e) {
                const node = e.target;
                const value = node.dataset.value;
                const text = node.textContent;
                this.valueChanged = true;
                const selecOption = {
                    value,
                    text
                };

                this.multi
                    ? this.multiSelectNext(selecOption)
                    : this.lonelySelectNext(selecOption)
            },
            multiSelectNext({value, text}) {
                if (this.isAlreadyActiveItem(value)) {
                    this.deleteActiveItem(value);
                    this.selectValueText = this.selectValueText
                        .split(",")
                        .filter((item) => item !== text)
                        .join(",");
                    return;
                }
                this.valueList.push(value);
                this.selectValueText = this.selectValueText.concat(`${text},`);
                this.setActiveItem(value);
                this.$emit("onSelect", this.valueList);
            },
            lonelySelectNext(option) {
                const {value, text} = option;
                this.deleteActiveItem(value);
                this.setActiveItem(value);
                this.selectValue = value;
                this.selectValueText = text;
                this.listOpen = false;
                this.$emit("onSelect", value);
            },
            findIndexItemByValue(value) {
                return this.listData
                    .findIndex(item => item.value === value);
            },
            setActiveItem(value) {
                const index = this.findIndexItemByValue(value);
                this.listData[index].active = true;
            },
            deleteActiveItem(value) {
                const index = this.findIndexItemByValue(value);
                if (index === -1) {
                    return;
                }

                if (this.multi) {
                    this.listData[index].active = false;
                    const indexValueList = this.valueList
                        .findIndex(item => item === value);
                    this.valueList = [...this.valueList.slice(0, indexValueList), ...this.valueList.slice(indexValueList, -1)];
                    if (this.valueList.length === 0) this.valueChanged = false;
                    this.$emit("onSelect", this.valueList);
                    return;
                }

                const indexLonely = this.listData.findIndex((item) => item.active === true);
                if (indexLonely !== -1) this.listData[indexLonely].active = false;
            },
            isAlreadyActiveItem(value) {
                return this.listData[this.findIndexItemByValue(value)].active === true;
            }
        }
    }
</script>

<style lang="scss">
  .option-select {
    position: relative;

    &_error &__title {
      border-color: red;
    }

    &_open &__title:after {
      transform: rotate(180deg);
    }

    &__title {
      display: flex;
      appearance: none;
      width: 100%;
      padding: 0.5rem 0.7rem;
      background-color: transparent;
      border: 1px solid #dcdfe6;
      box-shadow: none;
      border-radius: 0;
      font-size: .8rem;
      cursor: pointer;

      &:after {
        content: "▼";
        margin-left: auto;
        transition: transform .3s ease;
        will-change: transform;
      }
    }

    &__list {
      position: absolute;
      z-index: 20;
      top: 100%;
      width: 100%;
      background-color: #fff;
      border: 1px solid #dcdfe6;
      border-top: 0;
      border-radius: 2px;
      font-size: .9rem;
    }

    &__item {
      padding: 10px 15px;
      cursor: pointer;

      &:last-child {
        margin-bottom: 0;
      }

      &_active {
        background-color: #e7e7e7;
      }

      &:hover {
        background-color: #dcdfe6;
      }
    }
  }
</style>
