<template>
  <div class="option">
    <div v-if="error" class="option__status">{{msg}}</div>
    <input type="checkbox" @change="onChange" :checked="val"/>
  </div>
</template>

<script>
    export default {
        name: "Checkbox",
        props: {
            msg: {
                type: String,
                default: ""
            },
            error: {
                type: Boolean,
                default: false
            },
            val: {
                type: Boolean,
                default: false,
            },
            fieldName: {
                type: String
            }
        },
        methods: {
            onChange(e) {
                const value = e.target.checked;
                this.$emit("change", [value, this.fieldName]);
            }
        }
    }
</script>
