<template>
  <div class="overlay" :class="className">
    <div class="modal">
      <div class="modal__title">Новый пользователь успешно создан!</div>
      <div class="modal__content">
        <Button
          @click="closeModal"
          title="OK"
        />
      </div>
    </div>
  </div>
</template>

<script>
    import Button from "../ui/btn/Button";
    export default {
        name: "Modal",
        components: {Button},
        props: {
            isOpen: {
                type: Boolean,
                default: false,
            }
        },
        computed: {
            className() {
                return {
                    "overlay_open": this.isOpen,
                }
            }
        },
        methods: {
            closeModal(){
                this.$emit("close");
            }
        }
    }
</script>

<style lang="scss">
  .overlay {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .7);
    z-index: 30;

    &_open {
      display: block;
    }
  }

  .modal {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 400px;
    height: 200px;
    background-color: #fff;
    border: 1px solid #e7e7e7;
    box-shadow: 0 0 3px rgba(0, 0, 0, .4);
    font-size: 1.3rem;
    text-align: center;
    padding: 10px 5px;

    &__content {
      margin-top: 20px;
    }
  }
</style>
