<template>
  <div class="form__section">
    <div class="form__section-title">{{title}}</div>
    <div class="form__field" v-for="(field, index) in fields" :key="index">
      <div class="form__field-title" :class="field.required ? 'form__field-title_require':''">{{field.title}}</div>
      <Input @validate="onInput"
             :msg="msgErr(index)"
             :fieldName="index"
             :error="$v.fields[index].val.$error"
             :val="field.val"/>
    </div>
    <div class="form__field">
      <div class="form__field-title form__field-title_require">{{select.peopleGroups.title}}</div>
      <Select
        @onSelect="onMultiSelect"
        :msg="msgErrSelect('peopleGroups')"
        :error="$v.select.peopleGroups.selected.$error"
        :multi="true"
        :listData="select.peopleGroups.array"
        placeholder="Выбирите группу клиента"
      />
    </div>
    <div class="form__field">
      <div class="form__field-title">{{select.listDoctors.title}}</div>
      <Select
        @onSelect="onSelect"
        :listData="select.listDoctors.array"
        placeholder="Выбирите лечащего врача"
      />
    </div>
    <div class="form__field">
      <div class="form__field-title">{{checkboxes.sms.title}}</div>
      <Checkbox :val="checkboxes.sms.val"
                @change="onCheckBoxChange"
                fieldName="sms"/>
    </div>
    <Button
      title="Далее"
      @click="stepNext"
    />
  </div>
</template>

<script>
    import Input from "../ui/input/Input";
    import {required, numeric} from 'vuelidate/lib/validators';
    import {helpers} from 'vuelidate/lib/validators'
    import {
        MSG_REQUIRED,
        MSG_ONLY_NUMBER,
        MSG_EQUAL_LENGTH,
        MSG_ERROR_DATE,
        MSG_PHONE_START_SEVEN
    } from "../../utils/validator/consts";
    import Checkbox from "../ui/checkbox/Checkbox";
    import Select from "../ui/select/Select";
    import {getListDoctors, getPeopleGroup} from "../../api/fakeData";
    import Button from "../ui/btn/Button";

    export default {
        name: "FormSectionBaseInfo",
        components: {
            Select,
            Checkbox,
            Input,
            Button
        },
        props: {
            title: {
                default: ""
            }
        },
        created() {
            Promise.all([getListDoctors(), getPeopleGroup()])
                .then((res) => {
                    this.setListDoctors(res[0]);
                    this.setPeopleGroup(res[1]);
                })
                .catch((err) => console.error(err));
        },
        data() {
            return {
                fields: {
                    lastName: {
                        val: "",
                        title: "Фамилия",
                        required: true,
                    },
                    name: {
                        val: "",
                        title: "Имя",
                        required: true,
                    },
                    patronymic: {
                        val: "",
                        title: "Отчество",
                        required: false,
                    },
                    birthday: {
                        val: "",
                        title: "Дата рождения",
                        required: true,
                    },
                    phone: {
                        val: "",
                        title: "Номер телефона",
                        required: true,
                    },
                    gender: {
                        val: "",
                        title: "Пол",
                        required: false,
                    },
                },
                checkboxes: {
                    sms: {
                        val: false,
                        title: "Не отправлять СМС."
                    }
                },
                select: {
                    listDoctors: {
                        title: "Лечащий врач",
                        array: [],
                        selected: null,
                    },
                    peopleGroups: {
                        title: "Группа клиентов",
                        array: [],
                        selected: [],
                    }
                }
            }
        },
        validations: {
            fields: {
                lastName: {
                    val: {
                        required,
                    },
                },
                name: {
                    val: {
                        required,
                    },
                },
                patronymic: {
                    val: {}
                },
                birthday: {
                    val: {
                        required,
                        rightBirthDay: helpers.regex("rightBirthDay", /(0[1-9]|[12][0-9]|3[01])[- /.](0[1-9]|1[012])[- /.](19|20)\d\d/),
                    },
                },
                phone: {
                    val: {
                        required,
                        equalLength(val) {
                            return val.length === 11
                        },
                        startWithSeven(val) {
                            return val.startsWith("7")
                        },
                        numeric
                    },
                },
                gender: {
                    val: {}
                },
            },
            checkboxes: {
                sms: {
                    val: {}
                }
            },
            select: {
                peopleGroups: {
                    selected: {
                        required,
                    }
                }
            }
        },
        computed: {
            msgErr() {
                return (inputType) => {
                    if (this.$v.fields[inputType].val.required !== undefined && !this.$v.fields[inputType].val.required) return MSG_REQUIRED;
                    if (this.$v.fields[inputType].val.equalLength !== undefined && !this.$v.fields[inputType].val.equalLength) return MSG_EQUAL_LENGTH(11);
                    if (this.$v.fields[inputType].val.startWithSeven !== undefined && !this.$v.fields[inputType].val.startWithSeven) return MSG_PHONE_START_SEVEN;
                    if (this.$v.fields[inputType].val.rightBirthDay !== undefined && !this.$v.fields[inputType].val.rightBirthDay) return MSG_ERROR_DATE;
                    if (this.$v.fields[inputType].val.numeric !== undefined && !this.$v.fields[inputType].val.numeric) return MSG_ONLY_NUMBER;
                    return "Ошибка заполнения поля";
                }
            },
            msgErrSelect() {
                return (inputType) => {
                    if (!this.$v.select[inputType].selected.required) return MSG_REQUIRED;
                    return "deafult";
                }
            }
        },
        methods: {
            onInput([value, fieldName]) {
                this.fields[fieldName].val = value;
                this.$v.fields[fieldName].val.$touch();
            },
            onCheckBoxChange([value, fieldName]) {
                this.checkboxes[fieldName].val = value;
                this.$v.checkboxes[fieldName].val.$touch();
            },
            setListDoctors(array) {
                this.select.listDoctors.array = array;
            },
            setPeopleGroup(array) {
                this.select.peopleGroups.array = array;
            },
            onSelect(val) {
                this.select.listDoctors.selected = val;
            },
            onMultiSelect(val) {
                this.select.peopleGroups.selected = val;
                this.$v.select.peopleGroups.selected.$touch();
            },
            stepNext() {
                this.$v.$touch()
                if (!this.$v.$invalid) {
                    this.$emit("stepNext", {baseInfo: {...this.fields, ...this.checkboxes, ...this.select}})
                }
            }
        }
    }
</script>

<style lang="scss">

</style>
