<template>
  <button
    class="btn"
    type="button"
    :class="className"
    @click="buttonClick">
    <span>{{title}}</span>
  </button>
</template>

<script>
    export default {
        name: "Button",
        props: {
            title: {
                type: String,
                default: "Press"
            },
            disabled: {
                type: Boolean,
                default: false
            },
            error: {
                type: Boolean,
                default: false
            },
        },
        computed: {
            className() {
                return {
                    "btn_disabled": this.disabled,
                    "btn_error": this.error,
                }
            }
        },
        methods: {
            buttonClick() {
                this.$emit("click");
            }
        }
    }
</script>

<style lang="scss">
  .btn {
    cursor: pointer;
    display: inline-block;
    min-height: 1em;
    border: none;
    vertical-align: baseline;
    background: #e0e1e2 none;
    color: rgba(0,0,0,.6);
    margin: 0 .25rem 0 0;
    padding: .7rem 1.5rem .7rem;
    text-transform: none;
    text-shadow: none;
    font-weight: 700;
    line-height: 1em;
    font-style: normal;
    text-align: center;
    text-decoration: none;
    border-radius: .3rem;

    &_disabled {
      opacity: .7;
      cursor: not-allowed;
    }

    &_error {
      background-color: red;
      color: #fff;
    }
  }
</style>
