<template>
  <form
    @submit.prevent="onSubmit"
    class="form">
    <div v-show="stepActive === 1">
      <FormSectionBaseInfo
        @stepNext="stepNext"
        title="Базовая информация"/>
    </div>
    <div v-show="stepActive === 2">
      <FormSectionAddress
        @stepNext="stepNext"
        @stepPrev="stepPrev"
        title="Адресс"
      />
    </div>
    <div v-show="stepActive === 3">
      <FormSectionPassport
        @stepNext="stepNext"
        @stepPrev="stepPrev"
        title="Паспортные данные"
      />
    </div>
    <Modal
      :isOpen="this.isOpen"
      @close="closeModal"
    />
  </form>
</template>

<script>
    import FormSectionBaseInfo from "../form-sections/Form-section-base-info";
    import FormSectionAddress from "../form-sections/Form-section-address";
    import FormSectionPassport from "../form-sections/Form-section-passport";
    import Modal from "../modals/Modal";

    export default {
        name: "Form",
        components: {
            FormSectionBaseInfo,
            FormSectionAddress,
            FormSectionPassport,
            Modal
        },
        data() {
            return {
                stepActive: 1,
                steps: 3,
                formData: {},
                isOpen: false,
            }
        },
        methods: {
            stepNext(data) {
                if (this.stepActive <= this.steps) {
                    this.formData = {...this.formData, ...data};
                    this.stepActive = this.stepActive += 1;
                }
                if (this.stepActive > this.steps) {
                    this.onSubmit();
                    this.stepActive = 1;
                }
            },
            stepPrev() {
                if (this.stepActive >= 0) {
                    this.stepActive = this.stepActive -= 1;
                }
            },
            onSubmit() {
                this.isOpen = true;
            },
            closeModal() {
                this.isOpen = false;
            }
        }
    }
</script>

<style lang="scss">
  .form {
    width: 100%;
    max-width: 1290px;
    margin: 20px auto;
    padding: 20px 5px;
    border: 1px solid #e7e7e7;
    box-shadow: 0 7px 12px 0 #0000006e;
    border-radius: 6px;

    &__section {
      margin-bottom: 20px;
      padding-bottom: 10px;
      border-bottom: 1px solid #dcdfe6;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__section-title {
      margin-bottom: 1rem;
      font-size: 1.7rem;
    }

    &__field {
      margin-bottom: .8rem;
    }

    &__field-title {
      margin-bottom: .8rem;

      &_require {
        &:after {
          content: "*";
          color: red;
        }
      }
    }
  }
</style>
