<template>
  <div class="form__section">
    <div class="form__section-title">{{title}}</div>
    <div class="form__field">
      <div class="form__field-title form__field-title_require">{{select.passportSelect.title}}</div>
      <Select
        @onSelect="onSelect"
        :listData="select.passportSelect.array"
        :error="this.$v.select.passportSelect.selected.$error"
        :msg="selectMsg"
        placeholder="Тип предоставляемого документа"
      />
    </div>
    <div class="form__field" v-for="(field, index) in fields" :key="index">
      <div class="form__field-title" :class="field.required ? 'form__field-title_require':''">{{field.title}}</div>
      <Input @validate="onInput"
             :msg="msgErr(index)"
             :fieldName="index"
             :error="$v.fields[index] !== undefined ? $v.fields[index].val.$error : false"
             :val="field.val"/>
    </div>
    <Button
      title="Назад"
      @click="$emit('stepPrev')"
    />
    <Button
      title="Далее"
      @click="stepNext"
    />
  </div>
</template>

<script>
    import Input from "../ui/input/Input";
    import Select from "../ui/select/Select";
    import Button from "../ui/btn/Button";
    import {helpers, required} from 'vuelidate/lib/validators';
    import {
        MSG_REQUIRED,
    } from "../../utils/validator/consts";
    import {getPassportSelect} from "../../api/fakeData";

    export default {
        name: "FormSectionPassport",
        components: {
            Input,
            Button,
            Select
        },
        props: {
            title: {
                default: ""
            }
        },
        created() {
            getPassportSelect()
                .then((res) => this.setPassportSelect(res))
                .catch(err => console.log(err))
        },
        data() {
            return {
                fields: {
                    serial: {
                        val: "",
                        title: "Серия",
                        required: false,
                    },
                    number: {
                        val: "",
                        title: "Номер",
                        required: false,
                    },
                    WhoTake: {
                        val: "",
                        title: "Кем выдан",
                        required: false,
                    },
                    dateTake: {
                        val: "",
                        title: "Дата выдачи",
                        required: true,
                    },
                },
                select: {
                    passportSelect: {
                        title: "Тип документа",
                        array: [],
                        selected: null,
                    }
                },
            }
        },
        validations: {
            fields: {
                dateTake: {
                    val: {
                        required,
                        rightBirthDay: helpers.regex("rightBirthDay", /(0[1-9]|[12][0-9]|3[01])[- /.](0[1-9]|1[012])[- /.](19|20)\d\d/),
                    }
                },
            },
            select: {
                passportSelect: {
                    selected: {
                        required
                    }
                }
            }
        },
        computed: {
            msgErr() {
                return (inputType) => {
                    if (this.$v.fields[inputType] === undefined) return;
                    if (this.$v.fields[inputType].val.required !== undefined && !this.$v.fields[inputType].val.required) return MSG_REQUIRED;
                    return "Ошибка заполнения поля";
                }
            },
            selectMsg() {
                if (this.$v.select.passportSelect.selected.required) return MSG_REQUIRED;
                return "Ошибка заполнения поля";
            }
        },
        methods: {
            onInput([value, fieldName]) {
                this.fields[fieldName].val = value;
                if (this.$v.fields[fieldName] !== undefined) {
                    this.$v.fields[fieldName].val.$touch();
                }
            },
            stepNext() {
                this.$v.$touch();
                if (!this.$v.$invalid) {
                    this.$emit("stepNext", {passportInfo: {...this.fields, ...this.select}})
                }
            },
            setPassportSelect(array) {
                this.select.passportSelect.array = array;
            },
            onSelect(val) {
                this.select.passportSelect.selected = val;
                this.$v.select.passportSelect.selected.$touch();
            },
        }
    }
</script>


