<template>
  <div class="option">
    <div v-if="error" class="option__status">{{msg}}</div>
    <input type="text"
           :value="val"
           msg="default"
           @blur="onInput"
           class="option__input"
           :class="className"
    />
  </div>
</template>

<script>
    export default {
        name: "Input",
        props: {
            val: {
                type: String,
            },
            error: {
                type: Boolean,
                default: false
            },
            msg: {
                type: String,
            },
            fieldName: {
                type: String
            },
        },
        data() {
            return {}
        },
        computed: {
            className() {
                return {
                    "option__input_error": this.error
                }
            }
        },
        methods: {
            onInput(e) {
                const value = e.target.value;
                this.$emit("validate", [value, this.fieldName]);
            }
        }
    }
</script>

<style lang="scss">
  .option {
    margin-bottom: .9rem;

    &:last-child {
      margin-bottom: 0;
    }

    &__input {
      appearance: none;
      width: 100%;
      height: 2rem;
      padding: .5rem .7rem;
      background-color: transparent;
      border: 1px solid #dcdfe6;
      box-shadow: none;
      border-radius: .6em;

      &_error {
        border-color: red;
      }
    }

    &__status {
      margin: .5rem 0;
      font-size: .8rem;
      color: red;
    }
  }
</style>
