export function getListDoctors() {
    return new Promise((resolve) => {
        setTimeout(() => {
            resolve([
                {title: "Иванов", value: "1"},
                {title: "Захаров", value: "2"},
                {title: "Чернышева", value: "3"},
            ]);
        }, 1000)
    })
}

